/* You can add global styles to this file, and also import other style files */
@use "sass:map"
@use './node_modules/@angular/material' as mat

@import '~ngx-sharebuttons/themes/circles'
@import './dataculture'

body,html
  background-color: mat.get-color-from-palette($dataculture-grey-palette,800) !important

mat-drawer-container
  background: none !important

.app-button
  margin: 4px !important
  min-width: 100px !important


.app-card
  max-width: 500px
  min-width: 300px
  margin-left: 0px
  margin-right: 10px
  display: inline-block

.div-center
  width: 100%
  text-align: center

.icon-button
  width: 22px
  font-size: 1em
  height: 22px
  margin: 3px !important
  display: inline-block
  cursor: pointer

.small-icon-button
  margin: 0px !important
  padding: 0px
  display: inline-block
  cursor: pointer


.large-card
  min-width: 500px
  margin: 10px

.icon-profil
  width: 60px
  clip-path: circle()

.wrap-list
  display: flex
  flex-wrap: wrap
  justify-content: center
  align-items: flex-start
  min-height: 600px

::-webkit-scrollbar
    width: 0
    background: transparent

::-webkit-scrollbar-thumb
    background: transparent


//Le container contient le side-menu et le sidenavcontent
.toolbar-app
  height: 5vh
  background-color: mat.get-color-from-palette($dataculture-grey-palette,800)

.container
  width: 100vw
  height: 95vh
  overflow: scroll

.sidemenu
  height: 95vh
  padding: 0
  display: flex
  flex-direction: column

  overflow: hidden
  border: none
  background-color: mat.get-color-from-palette($dataculture-grey-palette,800)


.sidenav-content
  width: 100%
  height: 100%
  overflow: scroll
  justify-content: center
  text-align: center
  background-color: mat.get-color-from-palette($dataculture-grey-palette,900)

.mainform
  width: 100%
  text-align: center !important
  height: 100%
  background: none



.fake-button
  @extend .app-button
  width: 80px
  height: 26px
  text-align: center
  display: inline-block
  cursor: pointer
  line-height: 90%
  font-size: small
  padding: 10px


.sidenav
  padding: 0



.app-field
  width: 95%
  max-width: 600px
  margin: 3px
  height: min-content

h3
  width: 100%
  text-align: center
  font-size: x-large

.bottom-bar
  width: 100%
  z-index: 100
  padding-top: 5px
  padding-right: 5px
  text-align: right
  position: fixed
  display: block
  bottom: 0px
  right: 0px
  height: 50px


.h-zone
  margin: 5px
  font-size: x-large
  font-weight: lighter
  font-variant-caps: all-small-caps
  width: 100%
  text-align: center
  margin-bottom: 10px

.fill-remaining-space
  flex: 1 1 auto



.app-panel
  width: 100% !important



.faq-content
  padding-left: 5px
  font-size: small
  text-align: left



.bloc-bouton
  margin: 0
  padding: 0
  text-align: center
  display: inline-block
  font-size: small
  font-weight: 400
  line-height: 96%



mat-dialog-title
  font-style: normal
  font-weight: lighter
  font-size: medium

.app-mat-panel-title
  text-align: left
  font-weight: bold



.app-card-panel
  max-width: 500px
  width: 90%
  margin: 7px !important
  font-size: small
  display: inline-block

mat-icon
  color: mat.get-color-from-palette($dataculture-grey-palette,600)

a
  text-decoration: none


mat-body-2 > mat-card-title
mat-body-1 > mat-panel-title

.app-mat-panel-description
  font-weight: lighter
  text-align: left
  font-size: small
  vertical-align: center


mat-expansion-panel-header
  height: 45px !important
  padding-left: 10px !important
  font-weight: lighter !important
  width: 100%  !important

mat-expansion-panel-content
  padding: 2px !important
  margin: 0px !important
  width: 100%  !important


mat-action-row
  padding: 5px !important
  text-align: center


.removeBackground
  filter: blur(15px)
  cursor: pointer !important
  opacity: 0.7 !important
  pointer-events: none !important


.mini-icon-button
  font-size: 20px
  cursor: pointer
  margin: 2px
  display: inline


.dark_separator
  border: 1px solid black
  width: 100%
  margin-top: 20px
  margin-bottom: 10px


.search_zone
  max-width: 600px
  width: 60%
  font-size: large
  display: inline-block
  padding-left: 20px
  padding-right: 20px
  margin-right: 10px
  border: mat.get-color-from-palette($dataculture-grey-palette,700) solid 1px
  border-radius: 45px 45px


.fi-color
  color: $dataculture-blue

.material-icon.fi-color
  color: $dataculture-blue

.material-icon.fc-color
  color: $dataculture-green

.fc-color
  color: $dataculture-green


.flip-card
  max-width: 400px
  width: 100%
  text-align: left
  position: relative
  display: inline-block
  height: 150px
  perspective: 1000px


.flip-card-inner
  position: relative
  color: white !important
  width: 100%
  height: 100%
  transition: transform 0.6s
  transform-style: preserve-3d


.flip-card-inner-rotate
  position: relative
  color: white !important
  text-align: center
  transition: transform 0.6s
  transform-style: preserve-3d
  transform: rotateY(180deg)


.flip-card-front, .flip-card-back
  position: absolute
  border-radius: 10px 10px
  background-color: mat.get-color-from-palette($dataculture-grey-palette,800)
  padding: 10%
  width: 80%
  height: 100px
  -webkit-backface-visibility: hidden
  backface-visibility: hidden
  overflow: hidden
  transform: rotateX(0deg)

//https://stackoverflow.com/questions/9604982/backface-visibility-not-working-properly-in-firefox-works-in-safari

.flip-card-front
  color: black

.flip-card-back
  color: white
  position: relative
  transform: rotateY(180deg)

